





























export default {
	data() {
		return {
			list:[],
			top_stories:[]
		};
	},
	onLoad:function(){
		uni.request({
			url: 'https://news-at.zhihu.com/api/4/news/latest',
			method: 'GET',
			data: {},
			success: res => {
				this.list=res.data.stories;
				this.top_stories=res.data.top_stories
				console.log(this.top_stories);
			},
			fail: () => {},
			complete: () => {}
		});
	},
	methods:{
		openinfo(e){
			var id=e.currentTarget.dataset.id;
			uni.navigateTo({
				url: "../info/info?id="+id
			});
		}
	}
}



